var VehicleTable = React.createClass({
    render: function() {
        return (
            <table className="table table-striped table-bordered table-hover" id="vehicle-table">
                <TableHeader data={this.props.cols}></TableHeader>
                <TableBody colData={this.props.cols} rowData={this.state.data}></TableBody>
            </table>
        );
    },
    getInitialState: function () {
        return {data: []}
    },
    setPositionData : function (dataPosition) {
        this.setState({data: dataPosition});
    }
});

var TableHeader = React.createClass({
    render: function () {
        var headerComponents = this.generateHeaders();
        return (
            <thead className="tableHeader">{headerComponents}</thead>
        );
    },
    generateHeaders: function() {
        var cols = this.props.data;

        return cols.map(function(colData) {
            var styleHeader = {textAlign: 'center', height: '40px', backgroundColor: '#adebad'};
            if (!colData.display) { styleHeader['display'] = 'none'; }

            return <th className={colData.className} key={colData.source} style={styleHeader}>{colData.name}</th>;
        });
    }
});

var TableBody = React.createClass({
    render: function () {
        var rowComponents = this.generateRows();
        return (
            <tbody className="tableBody">{rowComponents}</tbody>
        );
    },
    generateRows: function() {
        var cols = this.props.colData,
            data = this.props.rowData;

        return data.map(function(item) {
            var cells = cols.map(function (colData) {
                var tdItem = _.get(item, colData.source);
                return <TableCell className={colData.className} key={colData.key} data={tdItem} dataConfig={colData}></TableCell>;
            });
            return <tr key={item.adevid}>{cells}</tr>;
        });
    }
});

var TableCell = React.createClass({
    render: function () {
        var dataCell = this.props.data;
        var config = this.props.dataConfig;
        var styleRow = {};

        if (config.dataType == 'number') {
            styleRow['textAlign'] = 'right';
        } else if (config.dataType == 'string') {
            styleRow['textAlign'] = 'left';
        } else if (config.dataType == 'icon') {
            styleRow['textAlign'] = 'center';
        }

        if (!config.display) { styleRow['display'] = 'none'; }

        if (config.key == 'd_checkbox') {
            return (
                <td className={config.className} style={styleRow}>
                    <input type="checkbox"></input>
                </td>
            );
        } else {
            return (
                <td className={this.props.className} style={styleRow}>
                    {dataCell}
                </td>
            );
        }
    },
    getSort: function () {
        
    }
});

var ButtonController = React.createClass({
    render: function () {
        return (
            <div className='buttonController'>
                {/*<BackwardButton/>*/}
                <PlayButton/>
                {/*<ForwardButton/>*/}
                <SliderButton/>
            </div>
        )
    }
});

var BackwardButton = React.createClass({
    render: function () {
        var styleButton = {
            marginTop: '14px',
            marginLeft: '11px',
            fontSize: '2.2em'
        };
        return (
            <span className="glyphicon glyphicon-backward" style={styleButton}></span>
        )
    }
});

var ForwardButton = React.createClass({
    render: function () {
        var styleButton = {
            marginTop: '14px',
            marginLeft: '18px',
            fontSize: '2.2em'
        };
        return (
            <span className="glyphicon glyphicon-forward" style={styleButton}></span>
        )
    }
});

var PlayButton = React.createClass({
    getInitialState() {
        return {
            currentIcon: 'glyphicon glyphicon-play'
        }
    },
    render: function () {
        var styleButton = {
            marginTop: '14px',
            marginLeft: '16px',
            fontSize: '2.2em',
            cursor: 'pointer'
        };
        return (
            <span className={this.state.currentIcon} style={styleButton} onClick={this.iconChange}></span>
        )
    },
    iconChange (e) {
        var iconType = e.target.className == 'glyphicon glyphicon-play';
        if (iconType) {
            this.setState({currentIcon: 'glyphicon glyphicon-pause'});
        } else {
            this.setState({currentIcon: 'glyphicon glyphicon-play'});
        }
    }
});

var SliderButton = React.createClass({
    render: function () {
        return (
                <div id="slider"></div>
        )
    }
});

